@charset "utf-8";
/* https://kuler.adobe.com/main-cxcedar-color-theme-4237607/ */
/* https://kuler.adobe.com/Copy-of-Copy-of-main-cxcedar-color-theme-4361434/ */
// $main-color : #2E313D; //333
@use "sass:color";

$max-width: 840px;

$main-color : #333;
$lighter-color: color.adjust($main-color, $lightness: 20%); //666
$lightest-color: color.adjust($main-color, $lightness: 40%); //999
$border-color: color.adjust($main-color, $lightness: 67%); //ddd
$darker-bg-color: color.adjust($main-color, $lightness: 77%); //f8f8f8
$bg-color: color.adjust($main-color, $lightness: 80%);


$header-color : #9C0; //#B7C11E;
$link-color : #008D00; //#23A38F;
$link-hover-color: color.adjust($link-color, $lightness: -20%);

$em-color : #F0563D; // 用于红色强调

$mark-color: #EFF1C2;

// $font-family : "Helvetica Neue", "Helvetica", Arial, "Hiragino Sans GB", "WenQuanYi Micro Hei", SimSun, sans-serif;
$font-family : "HelveticaNeue-Light","Helvetica Neue Light","Helvetica Neue",Helvetica,Arial,sans-serif;
$font-mono: Menlo, Monaco, "Andale Mono", "lucida console", "Courier New", monospace;
$font-Georgia: Georgia, serif;
$font-google-mono: "Cutive Mono", monospace;
// $font-Italianno: 'Italianno', cursive;

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
      -ms-border-radius: $radius;
          border-radius: $radius;
}

@mixin square($property){
	width: $property;
	height: $property;
}

/* don't support so many browser */
@mixin rotate($deg){
  transform:rotate($deg);
  -ms-transform:rotate($deg); 	/* IE 9 */
  -moz-transform:rotate($deg); 	/* Firefox */
  -webkit-transform:rotate($deg); /* Safari 和 Chrome */
  -o-transform:rotate($deg); 	/* Opera */
}

@mixin word-break(){
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
}

@mixin normal-color(){
  color: $main-color;
  background-color: $bg-color;
}

@mixin reverse-color(){
  color: $bg-color;
  background-color: $main-color;
}

.box { @include border-radius(10px); }

$normal-border: 1px solid $border-color;
